/**
 * SVG
 * @param require   require.context 
 * require.context('./svg', false, /\.svg$/)
 */
function loading(require) {
    const requireAll = requireContext => {
        requireContext.keys().map(requireContext);
    }
    requireAll(require);
}

export default {
    loading
};



